.userbar_mobile_menu {
	display: flex;
	column-gap: 6px;
	justify-content: space-between;
	align-items: center;
	position: relative;
}

.userbar_wrap {
	min-width: 100px;
	height: 32px;
	background-color: var(--color-main);
	border-radius: 30px;
	display: flex;
	column-gap: 6px;
	padding-right: 6px;
	justify-content: space-between;
	align-items: center;
	position: relative;
}

.userbar_transparent {
	background-color: transparent;
}

.userbar_avatar {
	width: 32px;
	height: 32px;
	border-radius: 50%;
	border: none;
	flex-shrink: 0;
}

.userbar_avatar_img {
	display: block;
	margin-left: auto;
	margin-right: auto;
	object-fit: cover;
	overflow: hidden;
	border-radius: 50%;
	border: none;
}

.userbar_dropdown_wrap {
	display: flex;
	column-gap: 4px;
	justify-content: space-between;
	align-items: center;
}

.userbar_dropdown_name {
	color: var(--color-white);
	font-size: 12px;
	font-weight: 700;
	line-height: 1.5;
	letter-spacing: -0.02em;
	text-transform: uppercase;
	max-width: 120px;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.userbar_chevron_down {
	display: flex;
	justify-content: center;
	align-items: center;
	transition: var(--transition);
	color: var(--color-white);
}

.userbar_chevron_down:hover,
.userbar_chevron_up:hover {
	color: var(--color-white-80);
}

.userbar_chevron_up {
	color: var(--color-white);
	transform: rotate(180deg);
	transition: var(--transition);
	height: 18px;
}

.userbar_icon_chevron {
	color: inherit;
	width: 18px;
	height: 18px;
}

.userbar_dropdown_menu {
	position: absolute;
	right: 6px;
	bottom: -80px;
	border-radius: 15px;
	width: 122px;
	padding: 15px;
	border: 1px solid var(--color-white-20);
	transition: all var(--transition);
	transform: scale(1);
}

.userbar_dropdown_menu_none {
	transform: scale(0);
	pointer-events: none;
	position: absolute;
	width: 0;
	bottom: 10px;
	right: 6px;
	transition: all var(--transition);
}

.userbar_dropdown_menu_list {
	color: var(--color-white);
	font-size: 12px;
	font-weight: 700;
	line-height: 1.5;
	letter-spacing: -0.02em;
	text-transform: uppercase;
	display: flex;
	flex-direction: column;
	row-gap: 4px;
}

.userbar_dropdown_menu_item {
	color: inherit;
}

.userbar_logout_arrow {
	height: 18px;
}

.color_black {
	color: var(--color-main);
	display: flex;
	column-gap: 2px;
	height: 18px;
	text-transform: uppercase;
	transition: all var(--transition);
}

.color_white {
	color: var(--color-white);
	display: flex;
	column-gap: 2px;
	height: 18px;
	text-transform: uppercase;
	transition: all var(--transition);
}

.color_black:hover, .color_white:hover, .color_black a:hover, .color_white a:hover {
	opacity: 50%;
	
}

.userbar_logout_icon {
	color: inherit;
	width: 18px;
	height: 18px;
	display: flex;
}

.burger_icon {
	width: 24px;
	height: 14px;
	position: relative;
	-webkit-transform: rotate(0deg);
	-moz-transform: rotate(0deg);
	-o-transform: rotate(0deg);
	transform: rotate(0deg);
	-webkit-transition: .5s ease-in-out;
	-moz-transition: .5s ease-in-out;
	-o-transition: .5s ease-in-out;
	transition: .5s ease-in-out;
	cursor: pointer;
}

.burger_icon span {
	display: block;
	position: absolute;
	height: 2px;
	width: 50%;
	background-color: var(--color-white);
	opacity: 1;
	-webkit-transform: rotate(0deg);
	-moz-transform: rotate(0deg);
	-o-transform: rotate(0deg);
	transform: rotate(0deg);
	-webkit-transition: .25s ease-in-out;
	-moz-transition: .25s ease-in-out;
	-o-transition: .25s ease-in-out;
	transition: .25s ease-in-out;
}

.burger_icon:hover span {
	opacity: 50%;
}

.burger_icon span:nth-child(even) {
	left: 50%;
	border-radius: 0 20px 20px 0;
}

.burger_icon span:nth-child(odd) {
	left: 0px;
	border-radius: 20px 0 0 20px;
}

.burger_icon span:nth-child(3) {
	width: 100%;
	border-radius: 20px;
}

.burger_icon span:nth-child(4) {
	width: 100%;
	left: 0px;
	border-radius: 20px;
}

.burger_icon span:nth-child(1),
.burger_icon span:nth-child(2) {
	top: 0px;
}

.burger_icon span:nth-child(3) {
	top: 4px;
}

.burger_icon span:nth-child(4) {
	top: 8px;
}

.burger_icon span:nth-child(5),
.burger_icon span:nth-child(6) {
	top: 12px;
}

.burger_icon.open span:nth-child(1),
.burger_icon.open span:nth-child(6) {
	-webkit-transform: rotate(45deg);
	-moz-transform: rotate(45deg);
	-o-transform: rotate(45deg);
	transform: rotate(45deg);
}

.burger_icon.open span:nth-child(2),
.burger_icon.open span:nth-child(5) {
	-webkit-transform: rotate(-45deg);
	-moz-transform: rotate(-45deg);
	-o-transform: rotate(-45deg);
	transform: rotate(-45deg);
}

.burger_icon.open span:nth-child(1) {
	left: 2px;
	top: 2px;
}

.burger_icon.open span:nth-child(2) {
	left: calc(50% - 3px);
	top: 2px;
}

.burger_icon.open span:nth-child(3) {
	left: -50%;
	opacity: 0;
}

.burger_icon.open span:nth-child(4) {
	left: 100%;
	opacity: 0;
}

.burger_icon.open span:nth-child(5) {
	left: 2px;
	top: 9px;
}

.burger_icon.open span:nth-child(6) {
	left: calc(50% - 3px);
	top: 9px;
}

.bg_black {
	background-color: var(--color-main);
}

@media screen and (min-width: 768px) {
	.userbar_mobile_menu {
		display: flex;
		column-gap: 6px;
		justify-content: space-between;
		align-items: center;
	}

	.userbar_wrap {
		height: 50px;
		padding-right: 14px;
	}

	.userbar_transparent {
		background-color: transparent;
	}

	.userbar_avatar {
		width: 50px;
		height: 50px;
		border-radius: 50%;
		border: none;
		flex-shrink: 0;
	}

	.userbar_dropdown_name {
		max-width: 115px;
	}

	.userbar_dropdown_menu {
		right: 14px;
		bottom: -86px;
		width: 148px;
		padding: 15px;
	}

	.userbar_dropdown_menu_none {
		right: 14px;
	}

	.userbar_dropdown_menu_list {
		row-gap: 6px;
	}

	.burger_icon {
		display: none;
		pointer-events: none;
	}
}

@media screen and (min-width: 1440px) {
	.userbar_mobile_menu {
		display: flex;
		column-gap: 6px;
		justify-content: space-between;
		align-items: center;
	}

	.userbar_transparent {
		background-color: transparent;
	}

	.userbar_dropdown_name {
		max-width: 300px;
	}
}